.landing .bg { height: 100vh; background-color: #254d9d; background-size: cover; background-repeat: no-repeat; margin: 0; display: flex; flex-direction: column; align-items: center; justify-content: center; }

.landing .bg-h { flex-direction: row; align-items: center; }

.landing .arrow-btn { color: #fff; }

.landing .arrow-btn:hover { color: #e6e6e6; }

.landing header { border-bottom: solid 1px rgba(255, 255, 255, 0.15); }

.landing .main-block { max-width: 600px; }

.landing .footer-wrapper, .landing footer { margin-top: 0; }

.landing.landing-bgimage { background-image: linear-gradient(90deg, rgba(63, 83, 162, 0.9) 0%, rgba(205, 10, 136, 0.9) 100%), url("../images/bg-imageDemo.png"); background-repeat: no-repeat; background-position: center; background-size: cover; }

.landing h1, .landing h2, .landing h3, .landing h4, .landing h5, .landing h6, .landing p, .landing strong, .landing em, .landing label, .landing .fa .nc-icon-mini, .landing .nc-icon-outline { color: #fff; }

.landing .mock-slider { display: flex; margin-top: 100px; justify-content: space-between; width: 90%; }

.landing .mock-slider .mock-slider-images { display: flex; width: 90%; justify-content: space-between; align-items: center; }

.landing .mid-block { display: flex; flex-direction: row; align-items: flex-start; justify-content: center; width: 90%; }

.landing .mid-block div { text-align: center; line-height: 1.5; width: 25%; padding: 0 30px; color: #fff; }

.landing .mid-block div h3 { color: #fff; }

.landing .btn-circle { font-family: "PrecisionSans_W_Rg", "Helvetica Neue", Arial, sans-serif; font-size: 18px; border: solid 1px #fff; border-radius: 100%; color: #fff; min-width: 47px; line-height: 47px; padding: 0; }

.landing .form-content { display: flex; flex-direction: column; align-items: center; }

.landing form.side-by-side { display: flex; flex-direction: row; justify-content: space-between; padding-top: 50px; width: 750px; margin: auto; }

.landing form.side-by-side .form-group { width: 45%; }

.landing input { color: #fff; background-color: rgba(255, 255, 255, 0.1); border-color: rgba(255, 255, 255, 0.8); }

.landing input:focus { border-color: white; background-color: rgba(255, 255, 255, 0.18); }

.landing input::-webkit-input-placeholder { color: #fff; }

.landing input:-moz-placeholder { color: #fff; }

.landing input::-moz-placeholder { color: #fff; }

.landing input:-ms-input-placeholder { color: #fff; }

.landing .hero { background-image: linear-gradient(90deg, #3e53a4 0%, #cf0989 100%); height: 40vh; margin-top: 50px; }

.landing h3 { color: #2e2e2e; }

.breadcrumb { padding: 0; background-color: transparent; margin-bottom: 5px; font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; font-size: 14px; }

.breadcrumb + h1 { margin-top: 0; }

.breadcrumb .active { color: #c0c0c0; }

html { height: 100%; }

body { min-height: 100%; display: flex; flex-direction: column; }

.site-content { flex: 1; margin-bottom: 40px; }

footer { background-color: #e1e1e1; padding: 10px 0; }

footer .container, footer .container-fluid { color: #717171; font-size: 12px; font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; z-index: 10; }

footer .container .contents, footer .container-fluid .contents { display: flex; flex-direction: row; align-items: baseline; justify-content: space-between; }

footer .container .logo-copyright, footer .container-fluid .logo-copyright { display: flex; flex-basis: 1; align-items: baseline; }

footer .container .logo-copyright .copyright, footer .container-fluid .logo-copyright .copyright { white-space: nowrap; }

footer .container .t-and-c, footer .container-fluid .t-and-c { white-space: nowrap; }

footer .container .footer-left, footer .container-fluid .footer-left { display: flex; align-items: baseline; }

footer .container .footer-left .logo-copyright, footer .container-fluid .footer-left .logo-copyright { margin-right: 40px; }

footer .container .contact-us, footer .container-fluid .contact-us { display: flex; align-items: baseline; white-space: nowrap; }

footer .container .contact-us .hidemethods, footer .container-fluid .contact-us .hidemethods { visibility: hidden; opacity: 0; transition: all .3s; }

footer .container .contact-us .contact-methods, footer .container-fluid .contact-us .contact-methods { display: flex; align-items: baseline; }

footer .container .contact-us .contact-methods > div, footer .container-fluid .contact-us .contact-methods > div { margin-left: 20px; }

footer .container .contact-us .contact-methods a, footer .container-fluid .contact-us .contact-methods a { color: #0072b8; }

footer .container .contact-us .contact-methods a.disabled, footer .container-fluid .contact-us .contact-methods a.disabled { color: #9b9b9b; }

footer .container .contact-us .contact-methods a.disabled:hover, footer .container-fluid .contact-us .contact-methods a.disabled:hover { background-color: transparent; cursor: not-allowed; text-decoration: none; }

footer .container > div, footer .container-fluid > div { padding: 0 20px 0 0; }

@media (max-width: 767px) { footer .container .contents { flex-direction: column; align-items: center; justify-content: space-around; }
  footer .container .contents .logo-copyright { flex-direction: column; align-items: center; margin-bottom: 10px; }
  footer .container .contents .logo-copyright .footer-logo { width: 85px; margin: 0 auto; }
  footer .container .contents .footer-left { flex-direction: column; align-items: center; }
  footer .container .contents .footer-left .logo-copyright { margin-right: 0; margin-bottom: 10px; }
  footer .container .contents .footer-right { flex-direction: column; align-items: center; }
  footer .container .contents .footer-right .contact-us { padding-top: 10px; text-align: center; }
  footer .container .contents .footer-right .contact-us .contact-methods.hidemethods { display: none; }
  footer > div { padding: 0 0 5px; margin: 0; } }

footer.demo { margin-top: 0; }

footer ul { margin: 0; }

footer ul.social li { padding-right: 1px; }

footer ul.social li:last-child { padding-right: 0; }

footer ul.social li a { color: #4e4e4e; transition: .2s all; display: inline-block; vertical-align: middle; height: 2em; line-height: 2em; }

footer ul.social li a:hover { transform: scale(1.3); }

